
.treeselect {
  position: relative;
  font-size: 16px;
  line-height: 21px;
}
.treeselect * {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.treeselect .clearfix, .treeselect.clearfix {
    content: "";
    display: table;
    clear: both;
}
.treeselect__select {
    cursor: pointer;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    width: 100%;
    height: 100%;
    padding: 5px 10px 3px 10px;
    font-size: inherit;
    line-height: inherit;
    border: 1px solid #E6E6E6;
    border-radius: 5px;
}
.treeselect__select--open {
      cursor: default;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
}
.treeselect__select input.treeselect__placeholder {
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      border: none;
      margin-top: 0;
      margin-bottom: 2px;
      padding-top: 5px;
      padding-bottom: 5px;
      font-size: 12px;
      line-height: 14px;
      background-color: transparent;
      text-overflow: ellipsis;
}
.treeselect__select input.treeselect__placeholder:focus, .treeselect__select input.treeselect__placeholder:active {
        outline: none;
}
.treeselect__flexbox {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    -ms-flex-line-pack: start;
        align-content: flex-start;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
}
.treeselect__clear-button, .treeselect__indicator {
    -ms-flex-item-align: end;
        align-self: flex-end;
    margin-left: 5px;
}
.treeselect__clear-button {
    cursor: pointer;
    margin-bottom: 5px;
    font-size: 12px;
    line-height: 12px;
    color: #CCCCCC;
}
.treeselect__indicator {
    margin-bottom: 7px;
    -webkit-transition: -webkit-transform 0.2s;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
    width: 0;
    height: 0;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-top: 9px solid #7B7B7B;
}
.treeselect__indicator--open {
      -webkit-transform: rotate3d(0, 0, 1, -180deg);
              transform: rotate3d(0, 0, 1, -180deg);
      border-top-color: #41b883;
}
.treeselect__dropdown {
    overflow: auto;
    z-index: 999;
    position: absolute;
    width: 100%;
    min-height: 10px;
    max-height: 200px;
    padding: 5px;
    border: 1px solid #E6E6E6;
    border-top: 0;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    background-color: white;
    z-index: 100;
}
.treeselect__dropdown-enter {
      max-height: 0;
      opacity: 0;
}
.treeselect__dropdown-enter-active {
      -webkit-transition: max-height 0.5s ease-out;
      transition: max-height 0.5s ease-out;
}
.treeselect__dropdown-leave-to {
      max-height: 0;
}
.treeselect__dropdown-leave-active {
      -webkit-transition: max-height 0.5s cubic-bezier(0.95, 0.05, 0.795, 0.035);
      transition: max-height 0.5s cubic-bezier(0.95, 0.05, 0.795, 0.035);
}



