
.treeselected {
  border-radius: 5px;
  margin-right: 2px;
  margin-bottom: 2px;
  max-width: 100%;
  float: left;
  font-size: 12px;
  line-height: 14px;
  color: white;
  background-color: #41b883;
  overflow: hidden;
  white-space: nowrap;
  background-color: #41b883;
  background: -webkit-gradient(linear, left bottom, left top, from(#009688), to(#41b883));
  background: linear-gradient(to top, #009688, #41b883);
}
.treeselected:hover {
    background: #338c64;
}
.treeselected__label {
    cursor: pointer;
    display: inline-block;
    padding: 5px 10px;
    white-space: pre;
}
.treeselected__delete-button {
    cursor: pointer;
    display: inline-block;
    text-align: center;
    border-left: 1px white solid;
    padding: 2px 6px 2px 5px;
}
.treeselected__delete-button:hover {
      background: #338c64;
}
.treeselected--has-delete-button .treeselected__label {
    cursor: default;
    padding-right: 2px;
}
.treeselected-enter {
    max-width: 0;
}
.treeselected-enter-active {
    -webkit-transition: max-width 0.3s cubic-bezier(0.95, 0.05, 0.795, 0.035);
    transition: max-width 0.3s cubic-bezier(0.95, 0.05, 0.795, 0.035);
}
.treeselected-leave-to {
    max-width: 0;
}
.treeselected-leave-active {
    -webkit-transition: max-width 0.2s cubic-bezier(0.95, 0.05, 0.795, 0.035);
    transition: max-width 0.2s cubic-bezier(0.95, 0.05, 0.795, 0.035);
}



